/* src/pages/HistoricData.css */

/* General page styling */
h1 {
    text-align: center;
    margin-bottom: 30px;
    font-size: 2rem;
    color: #ffffff;
}

.input-container {
    display: flex;
    flex-wrap: wrap; /* Allow wrapping if space is not enough */
    justify-content: flex-start;
    gap: 15px;
    margin-bottom: 20px;
    padding-left: 250px;
    align-items: center;
    padding-top: 100px;
}

/* Styling for individual input fields */
.input-group {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.input-group label {
    margin-bottom: 5px;
    font-size: 1rem;
    color: #ffffff;
}

.input-group input {
    padding: 8px;
    width: 200px;
    font-size: 1rem;
    border: 1px solid #ccc;
    border-radius: 5px;
    outline: none;
    transition: border-color 0.3s;
}

.input-group input:focus {
    border-color: #007bff;
}

button {
    padding: 10px 15px;
    margin-top: 30px;
    font-size: 1rem;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
    margin-left: 10px;
}

button:hover {
    background-color: #0056b3;
}

/* List of markets */
ul {
    margin-top: 20px;
    padding-left: 20px;
    list-style-type: none;
}

ul li {
    font-size: 1.1rem;
    color: #333;
    padding: 5px 0;
}

/* Message when no markets found */
p {
    font-size: 1rem;
    color: #ffffff;
}

/* Responsive styling */
@media (max-width: 768px) {
    .input-container {
        flex-direction: column;
        align-items: flex-start;
    }

    .input-group input {
        width: 100%;
    }

    button {
        width: 100%;
        margin-left: 0;
        margin-top: 10px;
    }
}
